.main-nav__link {
  color: #000000;
  text-decoration: none;
  font-family: "opensans-bold", Arial, sans-serif;
  font-size: 17px;
  font-weight: 700;
  width: 100%;
  display: block;
  background-color: #ffffff;
  padding: 28.5px 20px 26.5px 76px;
  box-sizing: border-box;
  box-shadow: 0 1px 0 0 #ececec, 0 1px 0 0 #ececec inset;
  position: relative;
  outline: none;
  transition: all 0.3s ease-in-out;
}
.main-nav__link--search::before {
  position: absolute;
  content: " ";
  width: 20px;
  height: 20px;
  mask-image: url("../../img/stack.svg#search");
  mask-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.2);
  left: 32px;
  top: 32px;
  transition: background-color 0.3s ease-in-out;
}
.main-nav__link--cart::before {
  position: absolute;
  content: " ";
  width: 20px;
  height: 20px;
  mask-image: url("../../img/stack.svg#cart");
  mask-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.2);
  left: 32px;
  top: 31px;
  transition: background-color 0.3s ease-in-out;
}
.main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link--search:hover::before,
.main-nav__link--cart:hover::before {
  background-color: #63d1bb;
}
.main-nav__link:active {
  opacity: 30%;
}
.main-nav__link:focus {
  color: rgba(0, 0, 0, 0.6);
}
@media (min-width: 768px) {
