}
@media (min-width: 1150px) {
  .main-nav__wrapper {
    display: flex;
    width: 1150px;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .main-nav__wrapper--closed {
    display: none;
  }
  .main-nav__wrapper--opened {
    display: block;
    position: absolute;
    top: 22px;
    left: 0;
    z-index: 10;
    width: 100%;
  }
}
.main-nav__wrapper--closed ~ .main-nav__toggle {
  width: 23px;
  height: 22px;
  background-color: transparent;
  border: none;
  position: absolute;
  cursor: pointer;
  right: 31px;
  top: -25px;
  z-index: 4;
}
@media (min-width: 768px) {
  .main-nav__wrapper--closed ~ .main-nav__toggle {
    display: none;
  }
}
.main-nav__wrapper--closed ~ .main-nav__toggle::before {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #231f20;
  z-index: 10;
  outline: none;
  transition: all 0.3s ease-in-out;
}
.main-nav__wrapper--closed ~ .main-nav__toggle::before:active::before {
  opacity: 30%;
}
.main-nav__wrapper--closed ~ .main-nav__toggle::before:hover::before,
.main-nav__wrapper--closed ~ .main-nav__toggle::before:focus::before {
  opacity: 60%;
}
.main-nav__wrapper--opened ~ .main-nav__toggle {
  position: absolute;
  width: 19px;
  height: 19px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  right: 32px;
  top: -28px;
  z-index: 10;
  transition: all 0.3s ease-in-out;
}
.main-nav__wrapper--opened ~ .main-nav__toggle::before,
.main-nav__wrapper--opened ~ .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  left: -3px;
  width: 27px;
  height: 2px;
  background-color: #000000;
  transition: all 0.3s ease-in-out;
}
.main-nav__wrapper--opened ~ .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__wrapper--opened ~ .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__wrapper--opened ~ .main-nav__toggle:active {
  opacity: 60%;
}
.main-nav__wrapper--opened ~ .main-nav__toggle:hover {
  opacity: 30%;
}
@media (min-width: 768px) {
  .main-nav__wrapper--opened ~ .main-nav__toggle::before,
  .main-nav__wrapper--opened ~ .main-nav__toggle::after {
    display: none;
  }
}
.main-nav__wrapper--nojs ~ .main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__wrapper--nojs.main-nav__wrapper--closed {
    position: static;
    display: block;
    margin-top: 15px;
  }
}
